.page-timer {
  color: #fff;
  max-width: 1000px;
  width: 100%;
  margin: 0 auto;
  min-height: 100vh;
  display: flex;
  align-items: center;
  &__container {
    margin: 0 auto;
  }
  @media (max-height: 700px) {
    padding-top: 30px;
  }
}
.time-container {
  position: relative;
  .timebgleft {
    position: absolute;
    left: -100px;
    top: 0;
    //transform: translate(-50%,-50%);
    @media (max-width: 992px) {
      top: -200px;
    }
    @media (max-width: 760px) {
      display: none;
    }
  }
  .timebgright {
    position: absolute;
    right: 0px;
    bottom: 0;
    width: 500px;
    height: 500px;
    opacity: 0.6;
    img {
      width: 100%;
      height: 100%;
    }
    //transform: translate(-50%,-50%);
    @media (max-width: 992px) {
      bottom: -150px;
    }
    @media (max-width: 760px) {
      display: none;
    }
  }
}
.js-timer {
  padding-bottom: 50px;
  margin-top: 90px;
  @media (max-height: 800px) {
    margin-top: 50px;
  }
 span {
   color: #e9e9e9!important;
   font-family   : "Antenna-Bold"!important;
   //font-size: 64px;
   @media (max-width: 992px) {
     font-size: 38px;
   }
   @media (max-width: 550px) {
     font-size: 30px;
   }
 }
  .timer-element {
    position: relative;
    min-width: 90px;
    @media (max-width: 992px) {
      min-width: 53px;
    }
    @media (max-width: 360px) {
      min-width: 43px;
    }
    &:before {
      content: "";
      width: 1px;
      height: 100px;
      background: #e9e9e9;
      right: -20px;
      z-index: 50;
      position: absolute;
      opacity: 0.45;
      @media (max-width: 992px) {
        height: 87px;
      }
      @media (max-width: 550px) {
        height: 65px;
        bottom: -20px;
      }
    }
    &:nth-child(1) {
      &:after {
        content: "Days";
        position: absolute;
        bottom: -25px;
        left: 50%;
        transform: translateX(-50%);
        font-family   : "Antenna-Bold"!important;
        @media (max-width: 992px) {
          font-size: 12px;
          bottom: -15px;
        }
      }
    }
    &:nth-child(3) {
      &:after {
        content: "Hours";
        position: absolute;
        bottom: -25px;
        left: 50%;
        transform: translateX(-50%);
        font-family   : "Antenna-Bold"!important;
        @media (max-width: 992px) {
          font-size: 12px;
          bottom: -15px;
        }
      }
    }
    &:nth-child(5) {
      &:after {
        content: "Minutes";
        position: absolute;
        bottom: -25px;
        left: 50%;
        transform: translateX(-50%);
        font-family   : "Antenna-Bold"!important;
        @media (max-width: 992px) {
          font-size: 12px;
          bottom: -15px;
        }
      }
    }
    &:nth-child(7) {
      &:after {
        content: "Seconds";
        position: absolute;
        bottom: -25px;
        left: 50%;
        transform: translateX(-50%);
        font-family   : "Antenna-Bold"!important;
        @media (max-width: 992px) {
          font-size: 12px;
          bottom: -15px;
        }
      }
      &:before {
        content: "";
        width: 1px;
        height: 100px;
        background: #e9e9e9;
        right: -20px;
        z-index: 50;
        position: absolute;
        opacity: 0.45;
        display: none;
      }
    }
  }
}
.js-timer > div {
  min-width: auto!important;
}

.logo {
  display: flex;
  justify-content: center;
  img {
    height: auto;
    @media (max-width: 768px) {
      width: 80%;
      height: 100%;
    }
  }
}
.description {
  font-family    : "Antenna-Medium";
  font-weight: 500;
  font-size: 36px;
  line-height: 104%;
  margin: 40px 0;
  text-align: center;
  p {
    white-space: nowrap;
  }
  @media (max-width: 768px) {
    font-size: 18px;
    line-height: 104%;
  }
}
.info-block {
  display: flex;
  flex-direction: column;
  align-items: center;
  @media (max-width: 625px) {
    padding: 0 25px;
  }
  &__top, &__bottom {
    display: flex;
    align-items: initial;
    justify-content: center;
    @media (max-width: 768px) {
      max-width: 620px;
      width: 100%;
    }
  }
  &__bottom {
    margin-top: 13px;
    @media (max-width: 768px) {
      margin-top: 7px;
    }
  }
  &__item {
    width: calc(50% - 0px);
    @media (max-height: 800px) {
      width: calc(40% - 0px);
    }
    img {
      &:last-child {
        display: none;
      }
    }
    @media (max-width: 768px) {
      width: calc(50% - 3px);
      img {
        &:first-child {
          display: none;
        }
        &:last-child {
          display: block;
        }
      }
    }
    img {
      width: 100%;
      transition: transform 0.3s;
      height: 100%;
      @media (max-width: 992px) {
        height: 100%;
      }
    }
    &:hover {
      img {
        transform: scale3d(1.02,1.02,1.02);
        transition: transform 0.3s;
      }
    }
  }

  &__audit {
    transform: translateX(15px);
    @media (max-width: 992px) {
      transform: translateX(10px);
    }
    @media (max-width: 768px) {
      width: calc(43% - 3px);
    }
    @media (max-width: 625px) {
      transform: translateX(5px);
    }
    @media (max-width: 375px) {
      transform: translateX(3px);
    }
  }
  &__whitepaper {
    transform: translateX(-15px);
    @media (max-width: 992px) {
      transform: translateX(-10px);
    }
    @media (max-width: 768px) {
      width: calc(57% - 3px);
    }
    @media (max-width: 625px) {
      transform: translateX(-5px);
    }
    @media (max-width: 375px) {
      transform: translateX(-3px);
    }
  }

  &__telegram {
    transform: translateX(15px);
    img {
      &:last-child {
        display: none;
      }
    }

    @media (max-width: 992px) {
      transform: translateX(10px);
    }
    @media (max-width: 768px) {
      width: calc(57% - 3px);
      img {
        &:first-child {
          display: none;
        }
        &:last-child {
          display: block;
        }
      }
    }
    @media (max-width: 625px) {
      transform: translateX(5px);
    }
    @media (max-width: 375px) {
      transform: translateX(3px);
    }
  }
  &__dx {
    transform: translateX(-15px);
    img {
      &:last-child {
        display: none;
      }
    }
    @media (max-width: 992px) {
      transform: translateX(-10px);
    }
    @media (max-width: 768px) {
      width: calc(43% - 3px);
      img {
        &:first-child {
          display: none;
        }
        &:last-child {
          display: block;
        }
      }
    }
    @media (max-width: 625px) {
      transform: translateX(-5px);
    }
    @media (max-width: 375px) {
      transform: translateX(-3px);
    }
  }
}
